@-webkit-keyframes show{
    0% {
        -webkit-transform: translate3d(0,1550px,-13999px) rotateX(90deg);
                transform: translate3d(0,1550px,-13999px) rotateX(90deg);
    }
    25% {
        -webkit-transform: translate3d(0,750px,-13999px) rotateX(80deg);
                transform: translate3d(0,750px,-13999px) rotateX(80deg);
    }
    50% {
        -webkit-transform: translate3d(0,450px,-10999px) rotateX(70deg);
                transform: translate3d(0,450px,-10999px) rotateX(70deg);
    }
    75% {
        -webkit-transform: translate3d(0,150px,-4999px) rotateX(30deg);
                transform: translate3d(0,150px,-4999px) rotateX(30deg);
    }
    100% {
        -webkit-transform: translate3d(0,0,0) rotateX(0deg);
                transform: translate3d(0,0,0) rotateX(0deg);
    }
}
@keyframes show{
    0% {
        -webkit-transform: translate3d(0,1550px,-13999px) rotateX(90deg);
                transform: translate3d(0,1550px,-13999px) rotateX(90deg);
    }
    25% {
        -webkit-transform: translate3d(0,750px,-13999px) rotateX(80deg);
                transform: translate3d(0,750px,-13999px) rotateX(80deg);
    }
    50% {
        -webkit-transform: translate3d(0,450px,-10999px) rotateX(70deg);
                transform: translate3d(0,450px,-10999px) rotateX(70deg);
    }
    75% {
        -webkit-transform: translate3d(0,150px,-4999px) rotateX(30deg);
                transform: translate3d(0,150px,-4999px) rotateX(30deg);
    }
    100% {
        -webkit-transform: translate3d(0,0,0) rotateX(0deg);
                transform: translate3d(0,0,0) rotateX(0deg);
    }
}
.birds-view.show .nav-wrap{
    -webkit-animation: show 0.8s 1 forwards linear;
            animation: show 0.8s 1 forwards linear;
}
